<template>
  <el-container>
    <el-header class="cheader">
      <el-row>
        <el-col :span="6">
          <h2>新建账号</h2>
        </el-col>
        <el-col :span="6"></el-col>
      </el-row>
    </el-header>

    <el-main class="cmain">
      <el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px">
        <el-form-item label="随机种子" prop="seed">
          <el-input v-model="ruleForm.seed" :disabled="true"></el-input>
          <el-button type="primary" size="small" @click="newSeed">重新生成</el-button>
        </el-form-item>
        <el-form-item label="请输入密码" prop="password1">
          <el-input placeholder="请输入密码" v-model="ruleForm.password1" show-password></el-input>
        </el-form-item>
        <el-form-item label="再次输入密码" prop="password2">
          <el-input placeholder="再次输入密码" v-model="ruleForm.password2" show-password></el-input>
        </el-form-item>
        <el-form-item label="账号别名" prop="alias">
          <el-input placeholder="12个字母数字以内" v-model="ruleForm.alias"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">创建</el-button>
          <el-button @click="accountList">取消</el-button>
        </el-form-item>
        <div>
          <p>注意：</p>
          <p>1. 账户=私钥（key)，是掌握您的账号的钥匙，请妥善保管。</p>
          <p>2. 私钥以加密形式保存，请在创建后，将私钥的加密文件单独备份。</p>
          <p>3. 密码用来加密您的私钥，请妥善设置，丢失无法找回。</p>
          <p>4. 别名仅在有多个账号的情况下，用做区分账号，仅此。</p>
        </div>
      </el-form>
    </el-main>
  </el-container>
</template>
<style>
</style>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.password2 !== "") {
          this.$refs.ruleForm.validateField("password2");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.password1) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var checkAlias = (rule, value, callback) => {
      if (value.length > 12) {
        callback(new Error("别名最长12个字符"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        seed: "",
        password1: "",
        password2: "",
        alias: ""
      },
      rules: {
        password1: [{ validator: validatePass, trigger: "blur" }],
        password2: [{ validator: validatePass2, trigger: "blur" }],
        alias: [{ validator: checkAlias, trigger: "blur" }]
      }
    };
    // const item = {
    //   alias: "default",
    //   key: "xadf24234234234234234234",
    //   info: "自动建立账号"
    // };
    // return {
    //   tableData: Array(20).fill(item)
    // };
  },
  mounted() {
    this.newSeed();
  },
  methods: {
    newSeed() {
      this.$axios({
        method: "post",
        data: JSON.stringify({
          id: new Date().getTime(),
          method: "account_makeEntropy",
          params: null
        })
      }).then(response => {
        if (typeof response.data.error != "undefined") {
          this.$alert("错误，请稍后重试", "seed", {
            showClose: false,
            confirmButtonText: "确定",
            callback: action => {}
          });
        } else {
          this.ruleForm.seed = response.data.result;
        }
      });
    },
    submitForm(formName) {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) {
          return false;
        }

        this.$axios({
          method: "post",
          data: JSON.stringify({
            id: new Date().getTime(),
            method: "account_newAccount",
            params: [
              this.ruleForm.alias,
              this.ruleForm.seed,
              this.ruleForm.password1
            ]
          })
        }).then(response => {
          if (typeof response.data.error != "undefined") {
            this.$alert("错误，请稍后重试", "新建账户", {
              showClose: false,
              confirmButtonText: "确定",
              callback: action => {}
            });
          } else {
            this.$alert("创建账号成功", "新建账户", {
              showClose: false,
              confirmButtonText: "确定",
              callback: action => {
                this.$router.push({ path: "/account" });
              }
            });
          }
        });
      });
    },
    accountList() {
      this.$router.push({ path: "/account" });
    },
    newAccount() {
      this.$router.push({ path: "/account/new" });
    }
  }
};
</script>